<template>
  <div>
    <!-- 面包屑 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>权限管理</el-breadcrumb-item>
      <el-breadcrumb-item>角色列表</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 列表卡片 -->
    <el-card class="box-card">
      <el-button type="primary" @click="addDialogFormVisible = true"
        >添加角色</el-button
      >

      <!-- 添加角色对话框 -->
      <el-dialog
        title="添加角色"
        :visible.sync="addDialogFormVisible"
        @close="addFormClose"
      >
        <!-- 添加角色表单区域 -->
        <el-form
          :model="addForm"
          :rules="addFormRules"
          ref="addFormRef"
          label-width="90px"
        >
          <el-form-item label="角色名称" prop="roleName">
            <el-input v-model="addForm.roleName"></el-input>
          </el-form-item>
          <el-form-item label="角色描述">
            <el-input v-model="addForm.roleDesc"></el-input>
          </el-form-item>
        </el-form>
        <!-- 底部按钮区域 -->
        <div slot="footer" class="dialog-footer">
          <el-button @click="addDialogFormVisible = false">取 消</el-button>
          <el-button type="primary" @click="addRole">确定</el-button>
        </div>
      </el-dialog>

      <!-- 角色列表 -->
      <el-table :data="this.$store.state.rolesList" border stripe>
        <!-- 展开列 -->
        <el-table-column type="expand">
          <template v-slot="data">
            <el-row
              :class="[
                'borderBottom',
                i1 == 0 ? 'borderTop' : '',
                'verticalCenter'
              ]"
              v-for="(item1, i1) in data.row.children"
              :key="item1.id"
            >
              <!-- 一级权限 -->
              <el-col :span="5">
                <el-tag closable @close="deleteRight(data.row, item1.id)">{{
                  item1.authName
                }}</el-tag>
                <i class="el-icon-caret-right"></i>
              </el-col>
              <!-- 二级权限和三级权限 -->
              <el-col :span="19">
                <el-row
                  :class="[i2 !== 0 ? 'borderTop' : '', 'verticalCenter']"
                  v-for="(item2, i2) in item1.children"
                  :key="item2.id"
                >
                  <!-- 二级权限 -->
                  <el-col :span="6">
                    <el-tag
                      type="success"
                      closable
                      @close="deleteRight(data.row, item2.id)"
                    >
                      {{ item2.authName }}
                    </el-tag>
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <!-- 三级权限 -->
                  <el-col :span="18">
                    <el-tag
                      type="warning"
                      v-for="item3 in item2.children"
                      :key="item3.id"
                      closable
                      @close="deleteRight(data.row, item3.id)"
                    >
                      {{ item3.authName }}
                    </el-tag>
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
          </template>
        </el-table-column>
        <!-- 索引列 -->
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column label="角色名称" prop="roleName"></el-table-column>
        <el-table-column label="角色描述" prop="roleDesc"></el-table-column>
        <el-table-column label="操作">
          <template v-slot="data">
            <!-- 编辑用户 -->
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="editFormShow(data.row.id)"
              >编辑</el-button
            >
            <!-- 删除用户 -->
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              @click="deleteRole(data.row.id)"
              >删除</el-button
            >
            <!-- 分配角色 -->
            <el-button
              type="warning"
              icon="el-icon-setting"
              size="mini"
              @click="allotRightsDialogShow(data.row)"
              >分配权限</el-button
            >
          </template>
        </el-table-column>
      </el-table>

      <!-- 修改编辑角色对话框 -->
            <el-dialog
              title="修改角色"
              :visible.sync="editDialogFormVisible"
              @close="editFormClose"
            >
              <!-- 修改编辑角色表单区域 -->
              <el-form
                :model="editForm"
                :rules="editFormRules"
                ref="editFormRef"
                label-width="70px"
              >
                <el-form-item label="角色名称" prop="roleName">
                  <el-input v-model="editForm.roleName"></el-input>
                </el-form-item>
                <el-form-item label="角色描述">
                  <el-input v-model="editForm.roleDesc"></el-input>
                </el-form-item>
              </el-form>
              <!-- 底部按钮区域 -->
              <div slot="footer" class="dialog-footer">
                <el-button @click="editDialogFormVisible = false"
                  >取 消</el-button
                >
                <el-button type="primary" @click="editRole">修改</el-button>
              </div>
            </el-dialog>

          <!-- 分配权限对话框 -->
            <el-dialog
              title="分配权限"
              :visible.sync="allotRightsDialogVisible"
              :lock-scroll="false"
              width="50%"
            >
            <!-- 树形控件 -->
              <el-tree
                :data="rightsTree"
                :props="rightsTreeProps"
                node-key="id"
                default-expand-all
                show-checkbox
                :default-checked-keys="defaultKeys"
                ref="rightsTreeRef"
              >
              </el-tree>
              <span slot="footer" class="dialog-footer">
                <el-button @click="allotRightsDialogVisible = false"
                  >取 消</el-button
                >
                <el-button
                  type="primary"
                  @click="allotRights"
                  >确 定</el-button
                >
              </span>
            </el-dialog>
    </el-card>
  </div>
</template>

<script>
export default {
  name: 'Roles',
  data() {
    return {
      rolesList: [],
      addDialogFormVisible: false,
      addForm: {
        roleName: '',
        roleDesc: ''
      },
      addFormRules: {
        roleName: [
          { required: true, message: '请输入角色名称', trigger: 'blur' }
        ]
      },
      editDialogFormVisible: false,
      editForm: {},
      editFormRules: {
        roleName: [
          { required: true, message: '请输入角色名称', trigger: 'blur' }
        ]
      },
      // 分配权限对话框显示
      allotRightsDialogVisible: false,
      // 树形控件所控制数据 权限列表数据树
      rightsTree: [],
      // 树形控件分级的属性依据和显示的级别文本
      rightsTreeProps: {
        children: 'children',
        label: 'authName'
      },
      // 默认勾选树结构数据
      defaultKeys: [],
      roleId: ''
    }
  },
  created() {
    this.getRolesList()
  },
  methods: {
    getRolesList() {
      this.$store.commit('getRolesList')
    },
    // 关闭对话框重置数据
    addFormClose() {
      this.$refs.addFormRef.resetFields()
    },
    // 添加角色
    addRole() {
      // 预验证
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await this.$http.post('roles', {
          roleName: this.addForm.roleName,
          roleDesc: this.addForm.roleDesc
        })
        if (res.meta.status !== 201) {
          return this.$message.error('添加角色失败')
        }
        // 添加成功
        this.$message.success('添加角色成功')
        // 关闭对话框
        this.addDialogFormVisible = false
        // 重新获取角色列表数据
        this.getRolesList()
      })
    },
    // 获取所要修改的角色数据
    async editFormShow(id) {
      const { data: res } = await this.$http.get(`roles/${id}`)
      if (res.meta.status !== 200) {
        return this.$message.error('获取所要修改角色数据错误')
      }
      // 获取成功弹出修改对话框
      this.editDialogFormVisible = true
      // 将获取到的数据保存到editForm中
      this.editForm = res.data
    },
    // 关闭编辑对话框重置数据
    editFormClose() {
      this.$refs.editFormRef.resetFields()
    },
    // 添加修改角色数据
    editRole() {
      // 预验证
      this.$refs.editFormRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await this.$http.put(
          `roles/${this.editForm.roleId}`,
          {
            roleName: this.editForm.roleName,
            roleDesc: this.editForm.roleDesc
          }
        )
        if (res.meta.status !== 200) return this.$message.error('修改角色失败')
        // 提示修改成功
        this.$message.success('修改角色成功')
        // 关闭修改对话框
        this.editDialogFormVisible = false
        // 重新获取角色列表数据
        this.getRolesList()
      })
    },
    // 删除角色
    async deleteRole(id) {
      const confirmResult = await this.$confirm(
        '此操作将永久删除该角色, 是否继续?',
        '提示',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }
      ).catch(err => err)

      if (confirmResult !== 'confirm') return this.$message.info('已取消删除')
      const { data: res } = await this.$http.delete(`roles/${id}`)
      if (res.meta.status !== 200) return this.$message.error('删除角色失败')
      // 提示删除成功
      this.$message.success('删除角色成功')
      // 重新获取角色列表数据
      this.getRolesList()
    },
    // 根据ID删除角色指定权限
    async deleteRight(role, id) {
      const confirmResult = await this.$confirm(
        '此操作将永久删除此权限, 是否继续?',
        '提示',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }
      ).catch(err => err)
      if (confirmResult !== 'confirm') {
        return this.$message.info('已取消删除权限')
      }
      const { data: res } = await this.$http.delete(
        `roles/${role.id}/rights/${id}`
      )
      if (res.meta.status !== 200) return this.$message.error('删除此权限失败')
      // 将返回的新的权限数据复制给父数据
      role.children = res.data
    },
    async allotRightsDialogShow(role) {
      // 获取权限列表数据
      const { data: res } = await this.$http.get('rights/tree')
      if (res.meta.status !== 200) { return this.$message.error('获取权限列表失败') }
      // 将获取到的权限列表数据树数据放到rightsTree
      this.rightsTree = res.data
      this.roleId = role.id
      // 将上一次获取的默认勾选权限清空
      this.defaultKeys = []
      // 获取默认的勾选权限
      this.setDefaultKeys(role, this.defaultKeys)
      // 弹出分配权限对话框
      this.allotRightsDialogVisible = true
    },
    // 将对应角色的所有权限ID放进defaultKeys数组中
    setDefaultKeys(node, array) {
      // 判断是否是三级权限，若为三级权限则将其ID放进array
      if (!node.children) return array.push(node.id)
      // 若不是三级权限，则进行递归
      node.children.forEach(item => {
        this.setDefaultKeys(item, array)
      })
    },
    // 修改权限后提交
    async allotRights() {
      // 将所有全选和半选中的权限ID取出
      const keys = [
        ...this.$refs.rightsTreeRef.getCheckedKeys(),
        ...this.$refs.rightsTreeRef.getHalfCheckedKeys()
      ]

      // 拼接为字符串
      const rids = keys.join(',')
      const { data: res } = await this.$http.post(`roles/${this.roleId}/rights`, { rids: rids })
      if (res.meta.status !== 200) return this.$message.error('更新角色权限失败')
      // 提示更新权限成功
      this.$message.success('更新角色权限成功')
      // 关闭分配权限对话框
      this.allotRightsDialogVisible = false
      // 重新获取角色列表数据
      this.getRolesList()
    }
  }
}
</script>

<style lang="less" scroped>
.el-tag {
  margin: 7px;
}

.borderTop {
  border-top: 1px solid #ccc;
}

.borderBottom {
  border-bottom: 1px solid #ccc;
}

.verticalCenter {
  display: flex;
  align-items: center;
}
</style>
