/* eslint-disable vue/no-unused-vars */
<template>
  <div>
    <!-- 面包屑 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>用户管理</el-breadcrumb-item>
      <el-breadcrumb-item>用户列表</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 列表卡片 -->
    <el-card class="box-card">
      <!-- 搜索框和添加按钮 -->
      <el-row :gutter="10" type="flex" justify="center">
        <el-col :span="10">
          <el-input v-model="queryInfo.query" clearable @clear="getUsersList">
            <el-button
              slot="append"
              icon="el-icon-search"
              @click="getUsersList"
            ></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="addDialogFormVisible = true"
            >添加用户</el-button
          >
        </el-col>
      </el-row>

      <!-- 用户列表表格 -->
      <el-table border stripe :data="usersList">
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column label="姓名" prop="username"></el-table-column>
        <el-table-column label="邮箱" prop="email"></el-table-column>
        <el-table-column label="电话" prop="mobile"></el-table-column>
        <el-table-column label="角色" prop="role_name"></el-table-column>
        <el-table-column label="状态">
          <template v-slot="data">
            <el-switch
              v-model="data.row.mg_state"
              @change="userStateChange(data.row)"
            >
            </el-switch>
          </template>
        </el-table-column>
        <el-table-column label="操作">
          <!-- 修改编辑 -->
          // eslint-disable-next-line vue/no-unused-vars
          <template v-slot="data">
            <el-tooltip
              effect="dark"
              content="编辑"
              placement="top"
              :enterable="false"
            >
              <el-button
                type="primary"
                icon="el-icon-edit"
                size="mini"
                @click="editFormShow(data.row.id)"
              ></el-button>
            </el-tooltip>
            <!-- 删除用户 -->
            <el-tooltip
              effect="dark"
              content="删除"
              placement="top"
              :enterable="false"
            >
              <el-button
                type="danger"
                icon="el-icon-delete"
                size="mini"
                @click="deleteUser(data.row.id)"
              ></el-button>
            </el-tooltip>
            <el-tooltip
              effect="dark"
              content="分配角色"
              placement="top"
              :enterable="false"
            >
              <el-button
                type="warning"
                icon="el-icon-setting"
                size="mini"
                @click="allotRoleDialogShow(data.row)"
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>

      <!-- 分页页码 -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 2, 3, 4]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>

    <!-- 添加用户对话框 -->
    <el-dialog
      title="添加用户"
      :visible.sync="addDialogFormVisible"
      @close="addFormClose"
    >
      <!-- 添加用户表单区域 -->
      <el-form
        :model="addForm"
        :rules="addFormRules"
        ref="addFormRef"
        label-width="70px"
      >
        <el-form-item label="用户名" prop="username">
          <el-input v-model="addForm.username"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password">
          <el-input v-model="addForm.password"></el-input>
        </el-form-item>
        <el-form-item label="邮箱" prop="email">
          <el-input v-model="addForm.email"></el-input>
        </el-form-item>
        <el-form-item label="手机" prop="mobile">
          <el-input v-model="addForm.mobile"></el-input>
        </el-form-item>
      </el-form>
      <!-- 底部按钮区域 -->
      <div slot="footer" class="dialog-footer">
        <el-button @click="addDialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="addUser">添加</el-button>
      </div>
    </el-dialog>

    <!-- 修改编辑用户对话框 -->
    <el-dialog
      title="修改用户"
      :visible.sync="editDialogFormVisible"
      @close="editFormClose"
    >
      <!-- 修改编辑用户表单区域 -->
      <el-form
        :model="editForm"
        :rules="editFormRules"
        ref="editFormRef"
        label-width="70px"
      >
        <el-form-item label="用户名">
          <el-input v-model="editForm.username" disabled></el-input>
        </el-form-item>
        <el-form-item label="邮箱" prop="email">
          <el-input v-model="editForm.email"></el-input>
        </el-form-item>
        <el-form-item label="手机" prop="mobile">
          <el-input v-model="editForm.mobile"></el-input>
        </el-form-item>
      </el-form>
      <!-- 底部按钮区域 -->
      <div slot="footer" class="dialog-footer">
        <el-button @click="editDialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="editUser">修改</el-button>
      </div>
    </el-dialog>

    <!-- 分配角色对话框 -->
    <el-dialog
      title="分配角色"
      :visible.sync="allotRoleDialogVisible"
      width="30%"
      @close="allotRoleDialogClose"
    >
      <p>用户名： {{ userInfo.username }}</p>
      <p>角色： {{ userInfo.role_name }}</p>
      <p>
        分配角色：
        <el-select v-model="selectRoleId" clearable placeholder="请选择">
          <el-option
            v-for="item in this.$store.state.rolesList"
            :key="item.id"
            :label="item.roleName"
            :value="item.id"
          >
          </el-option>
        </el-select>
      </p>
      <span slot="footer" class="dialog-footer">
        <el-button @click="allotRoleDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="allotRole">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'Users',
  data() {
    return {
      // 获取用户列表数据的参数对象
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 2
      },
      usersList: [],
      total: 0,
      addDialogFormVisible: false,
      addForm: {
        username: '',
        password: '',
        email: '',
        mobile: ''
      },
      addFormRules: {
        username: this.$store.state.username,
        password: this.$store.state.password,
        email: this.$store.state.email,
        mobile: this.$store.state.mobile
      },
      editDialogFormVisible: false,
      editForm: {},
      editFormRules: {
        email: this.$store.state.email,
        mobile: this.$store.state.mobile
      },
      // 分配角色对话框显示BOOLEAN
      allotRoleDialogVisible: false,
      // 所要分配角色所对应的用户信息
      userInfo: {},
      selectRoleId: '',
      addTitle: '添加用户'
    }
  },
  created() {
    this.getUsersList()
  },
  methods: {
    async getUsersList() {
      const { data: res } = await this.$http.get('users', {
        params: this.queryInfo
      })
      if (res.meta.status !== 200) {
        return this.$message({
          type: 'error',
          message: '获取用户数据失败',
          showClose: true,
          center: true
        })
      }
      this.usersList = res.data.users
      this.total = res.data.total
    },
    // 一页显示多少条 pagesize改变事件
    handleSizeChange(newPagesize) {
      this.queryInfo.pagesize = newPagesize
      this.getUsersList()
    },
    // 现在所处页码pagenum改变
    handleCurrentChange(newPagenum) {
      this.queryInfo.pagenum = newPagenum
      this.getUsersList()
    },
    // 用户状态改变
    async userStateChange(data) {
      const { data: res } = await this.$http.put(
        `users/${data.id}/state/${data.mg_state}`
      )
      if (res.meta.status !== 200) {
        data.mg_state = !data.mg_state
        return this.$message({
          type: 'error',
          message: '状态改变失败',
          showClose: true,
          center: true
        })
      }
      this.$message({
        type: 'success',
        message: '状态改变成功',
        showClose: true,
        center: true
      })
    },
    // 重置添加用户表单
    addFormClose() {
      this.$refs.addFormRef.resetFields()
    },
    // 添加用户事件
    addUser() {
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) return
        // 预验证通过后进行请求
        const { data: res } = await this.$http.post('users', this.addForm)
        // 添加失败
        if (res.meta.status !== 201) {
          return this.$message({
            type: 'error',
            message: '添加用户失败',
            showClose: true,
            center: true
          })
        }
        // 添加成功
        this.$message({
          type: 'success',
          message: '添加用户成功',
          showClose: true,
          center: true
        })
        // 重新获取用户列表数据以重新渲染页面
        this.getUsersList()
        // 关闭添加用户对话框
        this.addDialogFormVisible = false
      })
    },
    // 修改编辑对话框关闭重置数据
    editFormClose() {
      this.$refs.editFormRef.resetFields()
    },
    // 获取所要修改编辑用户的数据和弹出对话框
    async editFormShow(id) {
      const { data: res } = await this.$http.get(`users/${id}`)
      if (res.meta.status !== 200) {
        return this.$message({
          type: 'error',
          message: '获取所要修改用户数据失败',
          showClose: true,
          center: true
        })
      }
      // 弹出修改对话框
      this.editDialogFormVisible = true
      // 将获取的数据保存到editForm中
      this.editForm = res.data
    },
    // 修改用户提交数据
    editUser() {
      // 预验证
      this.$refs.editFormRef.validate(async valid => {
        // 验证不通过不能提交
        if (!valid) return
        const { data: res } = await this.$http.put(
          `users/${this.editForm.id}`,
          {
            email: this.editForm.email,
            mobile: this.editForm.mobile
          }
        )
        if (res.meta.status !== 200) {
          return this.$message({
            type: 'error',
            message: '修改用户数据失败',
            showClose: true,
            center: true
          })
        }
        // 修改提交成功后关闭对话框
        this.editDialogFormVisible = false
        // 提示修改成功
        this.$message({
          type: 'success',
          message: '修改用户数据成功',
          showClose: true,
          center: true
        })
        // 重新获取用户列表数据
        this.getUsersList()
      })
    },
    // 删除用户数据
    async deleteUser(id) {
      const confirmResult = await this.$confirm(
        '此操作将永久删除该用户, 是否继续?',
        '提示',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }
      ).catch(err => err)

      // 确定返回confirm  取消返回cancel
      // 点击取消提示
      if (confirmResult !== 'confirm') {
        return this.$message({
          type: 'info',
          message: '已取消删除',
          showClose: true,
          center: true
        })
      }

      // 点击确定删除用户
      const { data: res } = await this.$http.delete(`users/${id}`)
      // 刪除失敗提示
      if (res.meta.status !== 200) {
        return this.$message({
          type: 'error',
          message: '删除失敗!',
          showClose: true,
          center: true
        })
      }
      // 提示删除成功
      this.$message({
        type: 'success',
        message: '删除成功!',
        showClose: true,
        center: true
      })
      // 重新获取用户列表数据
      this.getUsersList()
    },
    async allotRoleDialogShow(userInfo) {
      // 存储所要修改角色的用户信息
      this.userInfo = userInfo
      // 获取角色数据
      this.$store.commit('getRolesList')
      // 弹出分配角色对话框
      this.allotRoleDialogVisible = true
    },
    async allotRole() {
      if (!this.selectRoleId) return this.$message.error('请选择要分配的角色')
      const { data: res } = await this.$http.put(
        `users/${this.userInfo.id}/role`,
        {
          rid: this.selectRoleId
        }
      )
      if (res.meta.status !== 200) return this.$message.error('分配角色失敗')
      // 提示分配成功
      this.$message.success('分配角色成功')
      // 重新獲取用戶列表數據
      this.getUsersList()
      // 关闭分配角色对话框
      this.allotRoleDialogVisible = false
    },
    allotRoleDialogClose() {
      this.selectRoleId = ''
    }
  }
}
</script>

<style lang="less" scoped></style>
